<template>
    <div class="statistic">
        <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
        <div class="title">问卷名称：{{name}}</div>
        <div class="content">
            <div class="item" v-for = "(item, index) in data" :key="index">
                <div class="title">{{item.title}}</div>   
                <div v-if="item.type == 0" class="myChart" :data-index="index" :style="{width: '400px', height: '400px'}"></div>
                <div class="list" v-if="item.type == 1">
                    <div class="list-item" v-for = "(item2, index2) in item.answerList" :key="index2">{{item2.userName}}: {{item2.content}}</div>
                </div>
            </div>
        </div>
        <!-- <div class="footer">
            <el-button @click="btnClick()" class="btn" type="primary">取消</el-button>
        </div> -->
    </div>
</template>

<script>
import Options from 'views/common/options';
import queryString from "querystring";
import { getSchool, getClass, getTeachers } from "data/listBody";

const dataOption = {
  leftMenu: [
    { id:'1', name:'校区：', placeholder:'请选择校区', module:'schoolId', type:'select'},
    { id:'2', name:'班级：', placeholder:'请选择班级', module:'classId', type:'select'},
    { id:'3', name:'带班老师：', placeholder:'请选择带班老师', module:'teacherAccount', type:'select'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r3', name:'取消'}
  ]
}

export default {
    name: "statistic",
    data() {
        return {
            dataOption: dataOption,
            loading: false,
            id: "",
            type: "",
            name: "",
            data: "",
        };
    },
    components: {
        Options,
    },
    mounted() {
        this.init();
        this.search();
        this.dropInit();
    },
    methods: {
        dropInit() {
            getSchool(res => {
                let { leftMenu } = this.dataOption;
                leftMenu = leftMenu.map(item => {
                if (item.module === "schoolId") item.options = res;
                return item;
                });
                this.dataOption.leftMenu = leftMenu;
            });
        },
        init() {
            const { type, id, name } = this.$route.query;
            this.type = type;
            this.id = id;
            this.name = name;
        },
        optchange(params){
            console.log(params.id + '--id被单击了')
            if(params.id === 'r1') return this.search();
            if(params.id === 'r3') return this.btnClick();
        },
        echartsInit() {
            const mycharts = document.querySelectorAll('.myChart');
            this.data.forEach((item, index) => {
                // 选择题
                if(item.type == 0){
                    const ansTotal = item.answerList.length;
                    let ansName = [item.item1, item.item2, item.item3, item.item4, item.item5];
                    let ansNum = [0, 0, 0, 0, 0];

                    item.answerList.forEach(obj => {
                        if(obj.item1) ansNum[0]++;
                        if(obj.item2) ansNum[1]++;
                        if(obj.item3) ansNum[2]++;
                        if(obj.item4) ansNum[3]++;
                        if(obj.item5) ansNum[4]++;
                    });
                    let myChart = this.echarts.init(mycharts[index]);
                    let seriesData = [];
                    ansName.forEach((item, index) => {
                        if(item){
                            let obj = {
                                value: ansNum[index],
                                name: item
                            }
                            seriesData.push(obj);
                        } 
                    })
                    myChart.setOption({
                        tooltip : {
                            trigger: 'item',
                            formatter: "{a} <br/>{b} : {c}人 ({d}%)",
                            position: [10, 10]
                        },
                        legend: {
                            // orient: 'vertical',
                            x: 'left',
                            data:ansName
                        },
                        series : [
                            {
                                name: item.title,
                                type: 'pie',
                                radius : ['50%', '70%'],
                                center: ['50%', '50%'],
                                avoidLabelOverlap: false,
                                label: {
                                    normal: {
                                        show: false,
                                        position: 'center'
                                    },
                                    emphasis: {
                                        show: true,
                                        textStyle: {
                                            fontSize: '16',
                                            fontWeight: '200'
                                        }
                                    }
                                },
                                // itemStyle: {
                                //     normal: {
                                //         label: {        //此处为指示线文字
                                //             show: true,
                                //             position: 'center', //标签的位置
                                //             textStyle: {
                                //                 fontWeight: 200,
                                //                 fontSize: 12    //文字的字体大小
                                //             },
                                //             formatter: function (p) {   //指示线对应文字
                                //                 var data = p.data;
                                //                 return data.name;
                                //             }
                                //         },
                                //         labelLine: {    //指示线状态
                                //             show: true,
                                //             smooth: 0.2,
                                //             length: 10,
                                //             length2: 20
                                //         }
                                //     }
                                // },
                                data: seriesData
                            }
                        ]
                    });
                }
                // 问答题
                if(item.type == 1){

                }
            })
        },
        search() {
            //搜索
            this.loading = true;
            const optParams = this.$refs.goodsOpt.getData();
            const params = Object.assign({}, optParams);
            const param = queryString.stringify(params || {});

            this.api.get(`/survey/${this.id}/answer?${param}`).then(res => {
                this.loading = false;
                this.data = res.data.questionList;
                setTimeout(()=>{
                    this.echartsInit(res);
                },20)
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },
        btnClick(){
            this.$router.go(-1)
        },
        dataInit() {
            //数据初始化
        }
    }
};
</script>
 
<style lang="less" scoped>
.statistic {
    .title,.content,.footer{
        padding: 40px;
    }
    .title{
        font-size: 18px;
        font-weight: 600;
        padding-bottom: 30px;
    }
    .item{
        .title{
            font-size: 16px;
            padding: 0;
            margin-bottom: 10px;
        }
        .list{
            padding-top: 10px;
            height: 100px;
            overflow: auto;
            width: 50%;
            .list-item{
                padding: 5px 0;
            }
        }
    }
    .footer{
        margin-top: 20px;
    }
}
</style>
 